import {
	mapState,
	mapActions,
	mapGetters,
	mapMutations
} from 'vuex'
import {
	uniPopup
} from "@dcloudio/uni-ui"
import TaskList from './taskList.vue'

export default {

	onLoad() {
		console.log('onLoad.....', this.dontTokens())
	},
	
	computed: {
		...mapState('authed', [
			'isLogin',
			'tokens',
		]),
		...mapState('home', [
			'taskList',
		])
	},
	
	components: {
		uniPopup,
		TaskList,
	},

	methods: {
		// ...mapActions('authed',['getTokenByCodeAction']),
		...mapGetters('authed', [
			'dontTokens',
		]),

		bindLogin() {
			uni.navigateTo({
				url: '../../components/login/loginContainer',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		}
	}
}
